let globalSetMode = () => {
  return {};
};

export const useBinancePairHistoricalPrice = singletonHook(BinancePairs, () => {
  const pool =
    (JSON.parse(
      window.localStorage.getItem("BinancePairHistoricalPrice") || "[]"
    ) as Erc20Price[]) || BinancePairs;

  const [mode, setMode] = useState<Erc20Price[]>(pool);
  //@ts-ignore
  globalSetMode = setMode;
  return mode;
});

export const setBinancePairHistoricalPrice = (pool: Erc20Price[]) => {
  //@ts-ignore
  globalSetMode(pool);
};

export interface Erc20Price {
  symbol: string;
  hrc20Address: string;
}
